  header,
  footer {
          margin: 4px;
          padding: 5px;
          min-height: 100px;
          border: 1px solid #eebb55;
          border-radius: 4pt;
          background: #2c7ec2;
          display: -ms-flexbox;
          display: flex;
          -ms-flex-pack: justify;
              justify-content: space-between;
          -ms-flex-align: center;
              align-items: center;
  }

  .button {
        background-color: rgb(179, 12, 34); /* Green */
        border: none;
        color: white;
        padding: 15px 32px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 16px;
        height: 70%;
        margin: 5px;
        cursor: pointer;
        position: relative;
    }

    .badge{
        content:"100";
        position: absolute;
        background: rgba(0,0,255,1);
        height:2rem;
        width:2rem;
        text-align: center;
        line-height: 2rem;;
        font-size: 1rem;
        border-radius: 50%;
        color:white;
        border:1px solid blue;
        margin-top: -0.5em;
        margin-left: -1.5em;
    }


